import { default as transModule } from '~components/i18n/trans';
import { default as initI18nTrigger,
  init as initialise,
  initI18nClass as initI18nClassExport,
} from '~components/i18n/i18next';
import { default as i18nextModule } from 'i18next';

export const init = initialise;
export const initI18n = initI18nTrigger;
export const trans = transModule;
export const initI18nClass = initI18nClassExport;
export const i18n = i18nextModule;



